import React, { useState, useRef} from 'react';
import { Button, Select, MenuItem, Container, Box, Grid, FormControl, InputLabel, TextField} from '@mui/material';
import axios from 'axios';
import './App.css';

const fuelTypeOptions = ['CNG', 'Diesel', 'LPG', 'Petrol'];
const transmissionOptions = ['Automatic', 'Manual'];
const brandOptions = ['Maruti', 'Hyundai', 'Honda', 'Audi', 'Nissan', 'Toyota', 'Volkswagen', 'Tata', 'Land', 'Mitsubishi', 'Renault', 'Mercedes-Benz', 'BMW', 'Mahindra', 'Ford', 'Porsche', 'Datsun', 'Jaguar', 'Volvo', 'Chevrolet', 'Skoda', 'Mini', 'Fiat', 'Jeep', 'Ambassador', 'Isuzu', 'ISUZU', 'Force', 'Bentley', 'Lamborghini']
const initialFormData = {
  year: '', 
  mileage: '',
  engine: '',
  power: '',
  normalized_kilometers: '',
  brand: '', 
  fuel_type: '',
  transmission: '',
};


function App(){
  const [formData, setFormData] = useState(initialFormData);
  const [responseText, setResponseText] = useState('');
  const [showResponse, setShowResponse] = useState(false);
  const responseSectionRef = useRef(null);

  const handleInputChange = (e) => {
    const { name, value } = e.target; 
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSubmit = async () => { 
    try {
      setShowResponse(true);
      responseSectionRef.current?.scrollIntoView({ behavior: 'smooth' });
      const response = await axios.post('http://localhost:5000/predict', formData, {
        headers: {
          'Content-Type': 'application/json',
        },
      }); 
      setResponseText(response.data);     
    } catch (error) {
      console.error('Error fetching data:', error);
      setResponseText('Error fetching data');
    }
   
  };
  return (
    <div className="app-container">
      <div className="form-container">
    <Container maxWidth="sm">
      <div className="custom-typography" variant="h4" align="center" gutterBottom>
        Enter your car for sale info
      </div>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <TextField
              label="Year"
              variant="outlined"
              fullWidth
              name="year"
              value={formData.year}
              onChange={handleInputChange}
              margin="normal"
              InputProps={{
                style: {
                  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', 
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Mileage"
              variant="outlined"
              fullWidth
              name="mileage"
              value={formData.mileage}
              onChange={handleInputChange}
              margin="normal"
              InputProps={{
                style: {
                  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', 
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Engine"
              variant="outlined"
              fullWidth
              name="engine"
              value={formData.engine}
              onChange={handleInputChange}
              margin="normal"
              InputProps={{
                style: {
                  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', 
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Power"
              variant="outlined"
              fullWidth
              name="power"
              value={formData.power}
              onChange={handleInputChange}
              margin="normal"
              InputProps={{
                style: {
                  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', 
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Normalized Kilometers"
              variant="outlined"
              fullWidth
              name="normalized_kilometers"
              value={formData.normalized_kilometers}
              onChange={handleInputChange}
              margin="normal"
              InputProps={{
                style: {
                  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', 
                },
              }}
            />
          </Grid>
          <Grid item xs={6}>
          <FormControl style={{  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', }} fullWidth variant="outlined" margin="normal">
              <InputLabel>Brand</InputLabel>
              <Select
                name="brand"
                value={formData.brand}
                onChange={handleInputChange}
                label="Brand"
              >
                {brandOptions.map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            </Grid>
          <Grid item xs={6}>
            <FormControl style={{  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', }}  fullWidth variant="outlined" margin="normal">
              <InputLabel>Fuel Type</InputLabel>
              <Select
                name="fuel_type"
                value={formData.fuel_type}
                onChange={handleInputChange}
                label="Fuel Type"
              >
                {fuelTypeOptions.map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={6}>
            <FormControl style={{  backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                  border: '1px solid rgba(0, 200, 0, 0.1)', 
                  borderRadius: '4px', }}  fullWidth variant="outlined" margin="normal">
              <InputLabel>Transmission</InputLabel>
              <Select
                name="transmission"
                value={formData.transmission}
                onChange={handleInputChange}
                label="Transmission"
              >
                {transmissionOptions.map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Box display="flex" justifyContent="center" mt={2}>
        <Button   className="customGreenButton" variant="contained" color="primary" onClick={handleSubmit}>
          Submit
        </Button>
        </Box>
      </Box>
    </Container>
    </div>
    <div ref={responseSectionRef} style={{ marginTop: '20px' }}>
      {showResponse && (
        <div style={{ width: '100%', opacity: '0.2', height: '300px', backgroundColor: 'brown', color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: '20px' }}>
          {responseText !== '' ? responseText : 'Waiting for response...'}
        </div>
      )}
      </div>
  </div>
  );
};

export default App;